
import { Cell } from "@/types/types";
import { defineComponent } from 'vue';

export default defineComponent({
  name: "GridCell",
  props: {
    i: {
      type: Number,
      required: true,
    },
    j: {
      type: Number,
      required: true,
    },
    isLive: {
      type: Boolean,
      required: true,
    },
    mouseIsDragging: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    handleCellClick() {
      this.$emit("toggleCell", new Cell(this.i, this.j));
    },
    handleMouseEnter() {
      if (this.mouseIsDragging) {
        this.handleCellClick();
      }
    },
  },
});
