
import { defineComponent } from 'vue';

export default defineComponent({
  name: "GameFooter",
  props: {
    playing: {
      type: Boolean,
      required: true,
    },
    generation: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      maxSliderSpeed: 1000,
      minSliderSpeed: 10,
    };
  },
  methods: {
    handleSliderChange(rawVal: number) {
      const valToSend = this.maxSliderSpeed - rawVal + this.minSliderSpeed;
      this.$emit('speedChanged', valToSend)
    },
  },
});
